[class*='btn'] {
  background-color: #ddd;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  outline: none;
  margin: 3px 0;
  padding: 10px 12px;
}
[class*='btn']:hover {
  background-color: #c7c7c7;
}
[class*='btn'] + [class*='btn'] {
  margin-left: 5px;
}
[class*='btn'].btn-primary {
  color: #fff;
  background-color: #08e;
}
[class*='btn'].btn-primary:hover {
  background-color: #007ad6;
}
[class*='btn'].btn-secondary {
  color: #fff;
  background-color: #1a1;
}
[class*='btn'].btn-secondary:hover {
  background-color: #0f990f;
}
[class*='btn'].btn-tertiary {
  color: #000;
  background-color: #eee;
}
[class*='btn'].btn-tertiary:hover {
  background-color: #d6d6d6;
}
[class*='btn'].btn-alert {
  color: #fff;
  background-color: #c22;
}
[class*='btn'].btn-alert:hover {
  background-color: #b81f1f;
}
[class*='btn'].btn-warning {
  color: #000;
  background-color: #fd0;
}
[class*='btn'].btn-warning:hover {
  background-color: #e6c700;
}
nav {
  background-color: #fff;
  line-height: 1em;
}
nav.full-width {
  float: left;
  margin: 0;
  padding: 0;
  width: 100%;
}
nav.full-width .brand {
  padding: 20px;
}
nav.full-width .brand img {
  max-height: 80px;
}
nav.full-width > * {
  margin: 0;
}
nav.full-width a {
  color: #666;
  float: left;
  text-decoration: none;
  transition: background-color 0.075s ease;
}
nav.full-width a:hover {
  background-color: #fafafa;
  color: #333;
}
nav.full-width a:not(.no-pad) {
  padding: 40px 20px;
}
nav.full-width a.no-pad {
  padding: 0;
}
html {
  box-sizing: border-box;
  font-family: 'Helvetica Neaue', 'Helvetica', 'Calibri', 'Arial', sans-serif;
  font-size: 1em;
  line-height: 1em;
}
html *,
html *:before,
html *:after {
  box-sizing: inherit;
}
body {
  background-color: #eee;
}
body > *:not([class*='xs-']):not(.full-width) {
  display: inline-block;
  margin: 20px 20px 0 20px;
